import utils from "../../../utils.js";
export default {
	onShareAppMessage: function(res) {
		
		if (res.from == 'button') {
			
		}
		return {
			title: '电子信息学校',
			path: '/pages/basic/login/login',
			imageUrl: 'http://data.huwei-soft.cn/schooltest/static/logo.png',
			success: function(res) {
				uni.showToast({
				    title: '分享成功',
				    duration: 2000
				});
			}
		};
	},
	data() {
		return {
			userInfo: {},
			todoNum: '',
			active: 1,
			scanMsg: '',
			scanType: '',
			header: '主页',
			menuArrow: true,
			menuBorder: false,
			menuCard: false,
		};
	},
	methods: {
		hide() {
			uni.hideLoading();
		},
		showModel(content) {
			uni.showModal({
			    title: '提示',
			    content: content,
					showCancel: false,
			    success: function (res) {
			        if (res.confirm) {
			            
			        }
			    }
			});
		},
		reloginCount() {
			uni.showModal({
				title: '提示',
				content: '退出登录?',
				success: function(res) {
					if (res.confirm) {
						uni.reLaunch({
							url: '/pages/basic/login/login'
						});
					} else if (res.cancel) {
						
					}
				}
			});
		},
		getTodoNum() {
			var self = this;
			uni
				.request({
					url: self.$store.state.urlPrefix + '/Process/Get_Mytask',
					data: {
						RYXH: self.$store.state.userInfo.RYXH
					}
				})
				.then(data => {
					var [error, res] = data;
					
					if (res.data == '暂无数据') {
						self.todoNum = '';
					} else {
						let data = JSON.parse(res.data);
						let len = data.length;
						len = len > 99 ? 99 : len;
						self.todoNum = len;
					}
				});
		},

		linkTo(name) {
			
			switch (name) {
				case 'home':
					this.active = 1;
					this.header = '首页';
					break;
				case 'search':
					this.active = 2;
					this.header = '搜索';
					break;
				case 'todo':
					this.active = 4;
					this.header = '待办事项';
					break;
				case 'my':
					this.active = 5;
					this.header = '个人中心';
					break;
			}
		},
		scanCode() {
			var self = this;
			uni.showLoading({
				title:'扫描中。。。',
				mask:true
			})
			uni.scanCode({
				success: function(res) {
					
					if (utils.GetUrlParam('id',res.result)) {
						let id = utils.GetUrlParam('id',res.result);
						let type = utils.GetUrlParam('type',res.result) || 'XF';
						// let type = 'XG';
						self.scanMsg = id;
						self.scanType = type;
						if (self.scanType == 'XF') {
							self.active = 3;
							self.header = '消防检查'
						} else if(self.scanType == 'XG'){
							self.active = 6;
							self.header = '巡更检查'
						} else {
							self.active = 7;
							self.header = '区域排查'
						}
						uni.hideLoading();
					} else {
						uni.hideLoading();
						self.showModel('扫描失败')
					}
				},
				fail() {
					self.active = 1;
					self.header = '首页'
					uni.hideLoading();
				}
			})
		},
		gotohome() {
			this.active = 1;
			this.header = '首页';
			this.getTodoNum();
		},
		toTodo() {
			this.active = 4;
			this.header = '待办事项';
			this.getTodoNum();
		}
	},
	mounted() {
		this.userInfo = this.$store.state.userInfo;
		
		this.getTodoNum();
	}
};
